<template>
  <v-container fluid>
    <v-card flat>
      <v-card-title>
        <v-spacer/>
        <v-text-field
          dense
          v-model="search"
          append-icon="mdi-search"
          label="Tm kim"
        />
      </v-card-title>
      <v-data-table
        :headers="headers"
        :items="thesis"
        :search="search"
      >
        <template
          v-slot:top
        >
          <v-toolbar flat color="white">
            <v-card-title>
              Danh sch  n
            </v-card-title>
            <v-spacer/>
          </v-toolbar>
        </template>
      </v-data-table>
    </v-card>
  </v-container>
</template>

<script>
import { mapGetters } from 'vuex';

export default {
  data() {
    return {
      dialog: false,
      menu: false,
      search: '',
      headers: [
        {
          text: 'Tn  n',
          value: 'name',
          filterable: true,
        },
        {
          text: 'Ging vin',
          value: 'teacher.fullName',
        },
        {
          text: 'Sinh vin',
          value: 'student.fullName',
        },
      ],
      editedItem: {},
    };
  },
  mounted() {
    this.$store.dispatch('loadThesis');
  },
  computed: {
    ...mapGetters(['thesis']),
    formTitle() {
      return this.editedItem._id ? 'Sa thng tin  n' : 'Thm thng tin  n';
    },
  },
  methods: {
    editItem(item) {
      this.editedItem = {
        _id: item._id,
        name: item.name,
        description: item.description,
        student: item.student._id,
        teacher: item.teacher._id,
        subject: item.subject._id,
        deadline: item.deadline,
      };
      this.dialog = true;
    },
    deleteItem(item) {
      this.$store.dispatch('deleteThesis', item);
    },
    close() {
      setTimeout(() => {
        this.editedItem = {};
      }, 300);
      this.dialog = false;
    },
    save() {
      if (this.editedItem._id) {
        this.$store.dispatch('updateThesis', this.editedItem);
      } else {
        this.$store.dispatch('createThesis', this.editedItem);
      }
      this.editedItem = {};
      this.dialog = false;
    },
  },
};
</script>
